import React from 'react';
import Highlight from 'react-highlight';
import cloneDeep from 'lodash.clonedeep';

import 'highlight.js/styles/github.css';
import './assets/styles.css';

const LINE_HEIGHT = 25

export default function eventLoopMachine(Ð¡omponent) {

  return class extends React.Component {

    constructor(props) {
      super(props);

      const initialState = {
        line: null,
        stack: [],
        queueTask: [],
        queueMicroTask: [],
        log: []
      };

      this.state = {
        machine: cloneDeep(initialState),
        step: -1,
        steps: [],
        cache: {
          '-1': cloneDeep(initialState)
        }
      };
    }

    canGoNext = () => (
      (this.state.step + 1) < this.state.steps.length
    )

    canGoPrev = () => (
      this.state.step > -1
    )

    handleStateNext = () => {
      if (this.canGoNext()) {
        const step = this.state.step + 1;

        if (this.state.cache[step]) {
          this.setState({
            machine: this.state.cache[step],
            step
          })
        } else {
          this.state.steps[step](() => {
            this.setState({
              step,
              cache: {
                ...this.state.cache,
                [step]: cloneDeep(this.state.machine)
              }
            })
          });
        }
      }
    }

    handleStatePrev = () => {
      if (this.canGoPrev()) {
        const step = this.state.step - 1;

        this.setState({
          machine: this.state.cache[step],
          step
        });
      }
    }

    setMachineStates = (steps) => {
      this.setState({
        ...this.state,
        steps
      })
    }

    moveCursorToLine = (line) => (callback) => {
      this.setState({
        machine: {
          ...this.state.machine,
          line: line
        }
      }, callback)
    }

    pushState = (stateKey) => (message) => (callback) => {
      const array = this.state.machine[stateKey]

      array.push(message)

      this.setState({
        machine: {
          ...this.state.machine,
          [stateKey]: array
        }
      }, callback)
    }

    popState = (stateKey) => () => (callback) => {
      const array = this.state.machine[stateKey]

      array.pop()

      this.setState({
        machine: {
          ...this.state.machine,
          [stateKey]: array
        }
      }, callback)
    }

    renderCursor = () => (
      this.state.machine.line && (
        <div ref="cursor" className="code__cursor" style={{
          top: LINE_HEIGHT * (this.state.machine.line - 1)
        }}/>
      )
    )

    renderCode = (code) => (
      <Highlight className="javascript">
        <pre>{code}</pre>
      </Highlight>
    )

    renderSection = (title, className, stateKey) => (
      <div className={className}>
        <div className={className + '__title'}>{title}</div>
        {this.state.machine[stateKey].map((message, key) => (
          <div className={className + '__item'} key={key}>
            {message}
          </div>
        ))}
      </div>
    )

    renderJavascriptStack = () => this.renderSection(
      'Javascript Stack',
      'js-stack',
      'stack'
    )

    renderQueueTask = () => this.renderSection(
      'Queue task',
      'queue-task',
      'queueTask'
    )

    renderQueueMicroTask = () => this.renderSection(
      'Queue micro task',
      'queue-micro-task',
      'queueMicroTask'
    )

    renderOutputLog = () => this.renderSection(
      'Output log',
      'log',
      'log'
    )

    renderControls = () => {
      return (
        <div className="controls">
          <button className="controls__button" onClick={this.handleStatePrev} disabled={!this.canGoPrev()}>
            Prev
          </button>
          <button className="controls__button" onClick={this.handleStateNext} disabled={!this.canGoNext()}>
            Next
          </button>
        </div>
      )
    }

    renderEventLoopMachine = (code) => (
      <div>
        <div className="code">
          {this.renderCursor()}
          {this.renderCode(code)}
          {this.renderControls()}
        </div>

        <div className="stacks">
          {this.renderJavascriptStack()}
          {this.renderQueueTask()}
          {this.renderQueueMicroTask()}
          {this.renderOutputLog()}
        </div>
      </div>
    )

    render() {
      return (
        <Ð¡omponent
          moveCursorToLine={this.moveCursorToLine}
          setMachineStates={this.setMachineStates}
          renderEventLoopMachine={this.renderEventLoopMachine}

          pushLog={this.pushState('log')}
          pushStack={this.pushState('stack')}
          pushQueueTask={this.pushState('queueTask')}
          pushQueueMicroTask={this.pushState('queueMicroTask')}

          popStack={this.popState('stack')}
          popQueueTask={this.popState('queueTask')}
          popQueueMicroTask={this.popState('queueMicroTask')} />
      );
    }
  }
}



