import React from 'react';
import PropTypes from 'prop-types';
import waterfall from 'async/waterfall';
import withEventLoopMachine from './EventLoopMachine';

class App extends React.Component {

  static propTypes = {
    moveCursorToLine: PropTypes.func,
    setMachineStates: PropTypes.func,
    renderEventLoopMachine: PropTypes.func,

    pushLog: PropTypes.func,
    pushStack: PropTypes.func,
    pushQueueTask: PropTypes.func,
    pushQueueMicroTask: PropTypes.func,

    popStack: PropTypes.func,
    popQueueTask: PropTypes.func,
    popQueueMicroTask: PropTypes.func
  }

  componentDidMount() {
    this.props.setMachineStates([
      (callback) => waterfall([
        this.props.moveCursorToLine(1),
        this.props.pushStack('script'),
        this.props.pushQueueTask('Run script'),
      ], callback),
      this.props.pushLog('script start'),
      this.props.moveCursorToLine(3),
      this.props.pushQueueTask('setTimeout callback'),
      this.props.moveCursorToLine(7),
      this.props.pushQueueMicroTask('Promise then'),
      this.props.moveCursorToLine(13),
      (callback) => waterfall([
        this.props.popStack('script'),
        this.props.pushLog('script end'),
      ], callback),


    ])
  }

  render() {
    return this.props.renderEventLoopMachine(`
      console.log('script start');

      setTimeout(function() {
        console.log('setTimeout');
      }, 0);

      Promise.resolve().then(function() {
        console.log('promise1');
      }).then(function() {
        console.log('promise2');
      });

      console.log('script end');
    `);
  }
}

export default withEventLoopMachine(App);



